import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ICommand, RxCommandFactoryService } from '@helix/platform/shared/api';

@Injectable({
  providedIn: 'root'
})
export class RxEventDefinitionService {
  private revertCustomizationCommand: ICommand;

  constructor(private rxCommandFactoryService: RxCommandFactoryService) {
    this.revertCustomizationCommand = this.rxCommandFactoryService.forResourceType(
      'com.bmc.arsys.rx.application.event.command.RevertEventDefinitionCommand'
    );
  }

  revertCustomization(eventDefinitionName: string): Observable<any> {
    return this.revertCustomizationCommand.execute({ eventDefinitionName });
  }
}
